import { useGoogleLogin } from "@react-oauth/google";
import axios from "axios";

const clientId = "YOUR_CLIENT_ID"; // Replace with your Google OAuth client ID
const redirectUri = "https://for-everything-priconne.netlify.app/login";

const onSuccess = async (tokenResponse) => {
  try {
    // Fetching userinfo can be done on the client or the server
    const userInfo = await axios
      .get("https://www.googleapis.com/oauth2/v3/userinfo", {
        headers: { Authorization: `Bearer ${tokenResponse.access_token}` },
      })
      .then((res) => res.data);

    if (userInfo) {
      await axios
        .post("https://priconne-backend.onrender.com/auth/google/refresh-token", {
          userInfo,
          tokenResponse,
        })
        .then((response) => {
          console.log(response);
          localStorage.setItem("mongoID", response.data.user._id);
          localStorage.setItem("id", userInfo.sub);
          window.location.reload();
        });
    }
  } catch (err) {
    console.error(err);
  }
};

const onError = (errorResponse) => {
  console.error(errorResponse);
};

export const useGoogleSignIn = () => {
  const signIn = useGoogleLogin({
    clientId,
    redirectUri,
    onSuccess,
    onError,
  });

  const handleGoogleSignIn = (e) => {
    e.preventDefault();
    signIn();
  };

  return handleGoogleSignIn;
};

const Signup = () => {
  const handleGoogleSignIn = useGoogleSignIn();

  return (
    <a href="/" className="button" onClick={handleGoogleSignIn}>
      <i className="fab fa-google left"></i> Login
    </a>
  );
};

export default Signup;
